<template>
  <div>
    <el-table :data="tableData">
      <el-table-column label="">
        <template slot-scope="scope">
          <a :href="scope.row.pic" target="_blank">
            <img :src="scope.row.pic" style="max-width: 100px;height: 50px"/>
          </a>
        </template>
      </el-table-column>
      <el-table-column prop="name" label=""></el-table-column>
      <el-table-column prop="content" label=""></el-table-column>
      <el-table-column prop="link" label=""></el-table-column>
      <el-table-column prop="ctime" :formatter="formatTime" label=""></el-table-column>
      <el-table-column width='150' label="">
        <template slot-scope="scope">
          <router-link :to="{name:'NewsAdd',params:{id: scope.row.id, row: scope.row}}">
            <el-button type="primary" size="small"></el-button>
          </router-link>
           <el-button type="danger" size="small" @click="del(scope.row.id, scope.$index)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  export default {
    name: 'NewsList',
    data() {
      return {
        username: '',
        tableData: [],
        totalNum: 0
      }
    },
    methods: {
      del(val, index) {
        let that = this
        that.$confirm(', ?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(() => {
          that.$axios.get('/api/news/delete/' + val).then(function (res) {
            if (res.status === 200 && res.data.code === 200) {
              that.tableData.splice(index, 1)
              that.$message({type: 'success', message: '!'})
            } else {
              that.$message({type: 'error', message: ''})
            }
          })
        }).catch(() => {
          that.$message({type: 'info', message: ''})
        })
      },
      query(that, params) {
        console.log(params)
        that.$axios.get('/api/news/list', {params: params}).then(function (res) {
          console.log(`ok`)
          if (res.status === 200 && res.data.code === 200) {
            that.tableData = res.data.data
          } else {
            that.tableData = []
          }
        }).catch((error) => {
          console.log(`err: ${error}`)
          that.tableData = []
        })
      },
      formatTime(row) {
        return this.$moment(row.ctime).format('YYYY-MM-DD HH:mm:ss')
      }
    },
    mounted() {
      this.query(this, {})
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-input {
    display: inline-block;
    width: 140px;
  }

  .el-select {
    width: 100px;
  }

</style>



