<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
    <el-form-item label="" prop="name">
      <el-input v-model="ruleForm.name" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="content">
      <el-input v-model="ruleForm.content" placeholder="" type="textarea"
                :autosize="{ minRows: 4, maxRows: 8}"></el-input>
    </el-form-item>
    <el-form-item label="" prop="link">
      <el-input v-model="ruleForm.link" placeholder=""></el-input>
    </el-form-item>
    <el-form-item label="" prop="pic">
      <el-upload
        class="upload"
        action="/api/user/upload"
        :headers="setHeaders"
        :on-preview="handlePreview"
        :on-remove="handleRemoveF"
        :before-upload="beforeUpload"
        :on-error="uploadError"
        :on-success="uploadSuccessF"
        name="image"
        :limit=1
        list-type="picture"
        :on-exceed="handleExceed"
        :file-list="fileListF">
        <el-button size="mini" type="primary">
          <i class="el-icon-upload el-icon--right"></i>
        </el-button>
        <div slot="tip" class="el-upload__tip"> jpg, png</div>
      </el-upload>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'NewsAdd',
    data() {
      return {
        pageStatus: 'add', //  add ,edit
        loading: false,
        setHeaders: {
          Authorization: `Bearer ${this.$store.state.token}`
        },
        ruleForm: {
          id: '',
          name: '',
          content: '',
          link: '',
          pic: ''
        },
        rules: {
          content: [{required: true, message: '', trigger: 'blur'}],
          name: [{required: true, message: '', trigger: 'blur'}],
          link: [{required: true, message: '', trigger: 'blur'}],
          pic: [{required: true, message: '', trigger: 'blur'}]
        },
        fileListF: []
      }
    },
    beforeMount() {
      let that = this
      that.pageStatus = 'add'
      let id = that.$route.params.id
      let row = that.$route.params.row
      if (id) {
        console.log('id:', that.id)
        that.pageStatus = 'edit'
        that.setDisabled = true
        that.ruleForm = row
        if (row.pic) {
          that.fileListF = [{name: row.pic, url: row.pic}]
        }
      }
    },
    methods: {
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let form = that.ruleForm
            console.log(form)
            let url = '/api/news/add'
            if (that.pageStatus === 'edit') {
              url = '/api/news/edit'
            }
            that.$axios.post(url, form).then(function (res) {
              if (res.status === 200 && res.data.code === 200) {
                that.$message({type: 'success', message: ''})
                that.$router.push({name: 'NewsList'}) // 
              } else {
                that.$message({type: 'error', message: ''})
              }
            }).catch(function (err) {
              console.log('err:')
              console.log(err)
              that.$message({type: 'error', message: ''})
            })
          }
        })
      },
      handleRemoveF(file, fileList) { // 
        this.ruleForm.pic = ''
      },
      handlePreview(file) { // ,  file.response 
        console.log('handlePreview:')
        console.log(file)
      },
      handleExceed(files, fileList) { // 
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `)
      },
      // 
      uploadSuccessF(response, file, fileList) {
        console.log('responseF:')
        console.log(response)
        if (response.code === 200) {
          console.log('upload ok')
          this.ruleForm.pic = response.data.relativeDir
          this.$message({message: '', type: 'success'})
        } else {
          this.$message({message: response.msg, type: 'error'})
        }
      },
      // 
      uploadError(err, file, fileList) {
        this.$message({message: '', type: 'error'})
        console.log(err)
      },
      beforeUpload(file) {
        console.log('file:')
        // file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif'
        let isJPG = (file.type === 'image/jpeg' || file.type === 'image/png')
        let isLtM = file.size / 1024 / 1024 < 10
        if (!isJPG) {
          this.$message.error('!')
        }
        if (!isLtM) {
          this.$message.error(' 10MB!')
        }
        return isJPG && isLtM
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-form {
    max-width: 700px;
  }
</style>



