<template>
  <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="150px" class="demo-ruleForm">
    <el-form-item label="" prop="type">
      <el-select v-model="ruleForm.type">
        <el-option
          v-for="item in type_option"
          :key="item.value"
          :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" prop="title">
      <el-input v-model="ruleForm.title"></el-input>
    </el-form-item>
    <el-form-item label="" prop="name">
      <el-input v-model="ruleForm.name"></el-input>
    </el-form-item>
    <el-form-item label="" prop="alias">
      <el-input v-model="ruleForm.alias"></el-input>
    </el-form-item>
    <el-form-item label="" prop="model">
      <el-input v-model="ruleForm.model"></el-input>
    </el-form-item>
    <el-form-item label="" prop="brand">
      <el-input v-model="ruleForm.brand"></el-input>
    </el-form-item>
    <el-form-item label="" prop="place">
      <el-input v-model="ruleForm.place"></el-input>
    </el-form-item>
    <el-form-item label="" prop="product">
      <el-input v-model="ruleForm.product"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="cooling_mode">
      <el-input v-model="ruleForm.cooling_mode"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="compressor">
      <el-input v-model="ruleForm.compressor"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1 || ruleForm.type===3" label="" prop="unit_category">
      <el-input v-model="ruleForm.unit_category"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1 || ruleForm.type===2" label="" prop="purpose">
      <el-input v-model="ruleForm.purpose"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="refrigerating_capacity">
      <el-input v-model="ruleForm.refrigerating_capacity"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="heat_production">
      <el-input v-model="ruleForm.heat_production"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="refrigeration_power">
      <el-input v-model="ruleForm.refrigeration_power"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="thermal_power">
      <el-input v-model="ruleForm.thermal_power"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="COP" prop="cop">
      <el-input v-model="ruleForm.cop"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="voltage_category">
      <el-input v-model="ruleForm.voltage_category"></el-input>
    </el-form-item>
    <el-form-item label="" prop="shape_size">
      <el-input v-model="ruleForm.shape_size"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="transport_weight">
      <el-input v-model="ruleForm.transport_weight"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1" label="" prop="operating_weight">
      <el-input v-model="ruleForm.operating_weight"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===1 || ruleForm.type===2" label="" prop="noise">
      <el-input v-model="ruleForm.noise"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===2 || ruleForm.type===3" label="" prop="air_volume">
      <el-input v-model="ruleForm.air_volume"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===2 || ruleForm.type===3" label="" prop="cooling_capacity">
      <el-input v-model="ruleForm.cooling_capacity"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===2 || ruleForm.type===3" label="" prop="heat_supply">
      <el-input v-model="ruleForm.heat_supply"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===2" label="" prop="static_pressure">
      <el-input v-model="ruleForm.static_pressure"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===2 || ruleForm.type===3" label="" prop="motor_power">
      <el-input v-model="ruleForm.motor_power"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===2 || ruleForm.type===3" label="" prop="weight">
      <el-input v-model="ruleForm.weight"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===3" label="" prop="tubes_num">
      <el-input v-model="ruleForm.tubes_num"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===3" label="" prop="residual_pressure">
      <el-input v-model="ruleForm.residual_pressure"></el-input>
    </el-form-item>
    <el-form-item v-if="ruleForm.type===3" label="" prop="working_condition">
      <el-input v-model="ruleForm.working_condition"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="submitForm('ruleForm')"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  export default {
    name: 'AirAdd',
    data() {
      return {
        pageStatus: 'add', //  add ,edit
        setHeaders: {
          Authorization: `Bearer ${this.$store.state.token}`
        },
        ruleForm: {
          id: '',
          type: 1, // 
          title: '', // 
          name: '', // 
          alias: '', // 
          model: '', // 
          brand: '', // 
          place: '', // 
          product: '', // 
          cooling_mode: '', // 
          compressor: '', // 
          unit_category: '', // 
          purpose: '', // 
          refrigerating_capacity: '', // 
          heat_production: '', // 
          refrigeration_power: '', // 
          thermal_power: '', // 
          cop: '', // COP
          voltage_category: '', // 
          shape_size: '', // 
          transport_weight: '', // 
          operating_weight: '', // 
          noise: '', // 
          air_volume: '', // 
          cooling_capacity: '', // 
          heat_supply: '', // 
          static_pressure: '', // 
          motor_power: '', // 
          weight: '', // 
          tubes_num: '', // 
          residual_pressure: '', // 
          working_condition: '' // 
        },
        rules: {},
        type_option: [
          {
            value: 1,
            label: ''
          }, {
            value: 2,
            label: ''
          }, {
            value: 3,
            label: ''
          }
        ]
      }
    },
    beforeMount() {
      let that = this
      that.pageStatus = 'add'
      let id = that.$route.params.id
      let row = that.$route.params.row
      if (id) {
        console.log('id:', that.id)
        that.pageStatus = 'edit'
        that.setDisabled = true
        that.ruleForm = row
      }
    },
    methods: {
      submitForm(formName) {
        let that = this
        this.$refs[formName].validate((valid) => {
          if (valid) {
            let form = that.ruleForm
            console.log(form)
            let url = '/api/air/add'
            if (that.pageStatus === 'edit') {
              url = '/api/air/edit'
            }
            that.$axios.post(url, form).then(function (res) {
              if (res.status === 200 && res.data.code === 200) {
                that.$message({type: 'success', message: ''})
                that.$router.push({name: 'AirList'}) // 
              } else {
                that.$message({type: 'error', message: ''})
              }
            }).catch(function (err) {
              console.log('err:')
              console.log(err)
              that.$message({type: 'error', message: ''})
            })
          }
        })
      },
      handleRemoveF(file, fileList) { // 
        this.ruleForm.avatar = ''
      },
      handlePreview(file) { // ,  file.response 
        console.log('handlePreview:')
        console.log(file)
      },
      handleExceed(files, fileList) { // 
        this.$message.warning(` 1  ${files.length}  ${files.length + fileList.length} `)
      },
      // 
      uploadSuccessF(response, file, fileList) {
        console.log('responseF:')
        console.log(response)
        if (response.code === 200) {
          console.log('upload ok')
          this.ruleForm.avatar = response.data.relativeDir
          this.$message({message: '', type: 'success'})
        } else {
          this.$message({message: response.msg, type: 'error'})
        }
      },
      // 
      uploadError(err, file, fileList) {
        this.$message({message: '', type: 'error'})
        console.log(err)
      },
      beforeUpload(file) {
        console.log('file:')
        console.log(file)
        // file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'image/gif'
        let isJPG = (file.type === 'image/jpeg' || file.type === 'image/png')
        let isLtM = file.size / 1024 / 1024 < 10
        if (!isJPG) {
          this.$message.error('!')
        }
        if (!isLtM) {
          this.$message.error(' 10MB!')
        }
        return isJPG && isLtM
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .el-form {
    max-width: 450px;
  }
</style>



